<template>
  <el-container>
    <el-main v-loading="loading">
      <el-table :data="branchList">
        <el-table-column prop="id" label="分院ID" />
        <el-table-column prop="name" label="分院名称" />
        <el-table-column prop="area" label="地区" />
        <el-table-column prop="address" label="分院地址" />
        <el-table-column prop="workTime" label="营业时间" />
        <el-table-column prop="phone" label="联系电话" />
        <el-table-column label="操作" width='220'>
          <template slot-scope="scope">
            <el-button @click="handleClick(scope.row)" type="text" size="small">编辑</el-button>
            <el-button v-show="code == 0" type="text" size="small" @click="delMsg(scope.row)">下线</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-dialog
        title="提示"
        :visible.sync="dialogVisible"
        width="30%">
        <span>确认下线该条信息？</span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="sure">确 定</el-button>
        </span>
      </el-dialog>
    </el-main>
  </el-container>
</template>
<script>
// import { phyexaminInfo } from "../../../../data/hosNetwork";
let urls = ['/physical','/chinaMed','/dentistry'];
export default {
  name: "branchTable",
  props:['data', 'loading'],
  data() {
    return {
      branchList: [],
      // branchList: Array(8).fill(phyexaminInfo),
      dialogVisible: false,
      id:'',
      code:'',
      codeId:'',
    };
  },
  mounted(){
    if( this.$route.query ){
      this.code = this.$route.query.code;
      this.codeId = this.$route.query.id;
    } 
  },
  methods: {
    handleClick(params) {
      this.id = params.id;
      this.$router.push(`branchAdd?type=edit&code=${this.code}&codeId=${this.codeId}&id=${this.id}`);
    },  
    delMsg(params) {
      this.id = params.id;
      this.dialogVisible = true;
    },
    sure(){
      let url = urls[this.code];
      this.api.delete(`${url}/${this.id}`).then(()=>{
        this.branchList = this.branchList.filter(item=>{
          return item.id !== this.id
        })
      }).catch((err) => {
        console.log(err)
        this.$message(err.data.message)
      })
      this.dialogVisible = false
    }   
  },
  watch:{
    data(newValue){
      this.branchList = newValue;
    }
  }
};
</script>
<style lang="less" scoped>
</style>